@import url(https://fonts.googleapis.com/css?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.switch-wrapper {
  position: relative;
}

.switch-wrapper > div {
  position: absolute;
}
button {
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 50px;
    height: 67px;
    padding: 20px;
    margin: 30px;
    /* TODO: re-add outline for accessibility */
    outline: none;
}

button span {
    font-family: 'PT Sans', sans-serif;
    font-size: 24px;
    color: #343434;
}
.home {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column; }
  .home > img {
    padding-top: 5%; }
  .home .pattern {
    position: absolute;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background: url("/eishow-alpha/static/media/BG pattern.f8b38794.svg");
    -webkit-animation-name: moveBG;
            animation-name: moveBG;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear; }

footer {
  visibility: hidden;
  position: fixed;
  bottom: 10px;
  width: 100vw;
  display: flex;
  justify-content: center; }
  footer span {
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8); }

@-webkit-keyframes moveBG {
  from {
    background-position-x: 0px; }
  to {
    background-position-x: -200px; } }

@keyframes moveBG {
  from {
    background-position-x: 0px; }
  to {
    background-position-x: -200px; } }

.video-page .video-container {
  width: 100vw;
  height: 240px;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0px;
  background-color: black; }
  .video-page .video-container img {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1; }
  .video-page .video-container video {
    overflow: auto; }

.video-page .text-container {
  padding-top: 240px; }

.video-page h1 {
  text-align: center; }

.video-page p {
  margin-left: 25px;
  margin-right: 25px;
  text-align: justify; }

