button {
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 50px;
    height: 67px;
    padding: 20px;
    margin: 30px;
    /* TODO: re-add outline for accessibility */
    outline: none;
}

button span {
    font-family: 'PT Sans', sans-serif;
    font-size: 24px;
    color: #343434;
}