.video-page {
    $videoContainerSize: 240px;

    .video-container {
        width: 100vw;
        height: $videoContainerSize;
        display: flex;
        justify-content: center;

        position: fixed;
        top: 0px;
        
        background-color: black;
        
        img {
            position: absolute;
            top: 20px;
            left: 20px;
            z-index: 1;
        }

        video {
            overflow: auto;
        }
    }

    // we're not setting a background for text (yet)

    .text-container {
        padding-top: $videoContainerSize;
    }

    h1 {
        text-align: center;
    }

    p {
        margin-left: 25px;
        margin-right: 25px;
        text-align: justify;
    }
}